<script>
    import petApiService from '../../services/http/petApiService.js';
    import PetStatus from '../../models/enums/petStatus.js';

    let petList = [];
    let status = PetStatus.available;
    let isLoading = false;

    function onChangeStatus(){
        petList = [];
        findByStatus(status);    
    }

    async function findByStatus(){
        petList = await petApiService.findByStatus(status);
    }

    findByStatus(PetStatus.available);

</script>

<div>
    <button on:click={onChangeStatus}>List</button>
    <select bind:value={status} on:change={onChangeStatus}>
        <option selected value={PetStatus.available}>Available</option>
        <option value={PetStatus.pending}>Pending</option>
        <option value={PetStatus.sold}>Sold</option>
    </select>

    {#if isLoading}
        <span>Loaging...</span>
    {/if}

    {#each petList as item}
        
        {#if item && item.photoUrls && item.photoUrls[0] != "string"}
            <p>{JSON.stringify(item.name)}</p>
            <img src={item.photoUrls[0]} alt={item.name} />
        {/if}
    {/each}
</div>

<style>
    img{
        max-width: 400px;
    }
</style>
